<template>
  <div class="config-main">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button size="small" icon="el-icon-plus" type="warning" @click="projectVisible = true">添加</el-button>
    </div>
    <el-table :data="project.list" stripe>
      <el-table-column
        prop="appId"
        label="AppId">
      </el-table-column>
      <el-table-column
        prop="name"
        label="应用名">
      </el-table-column>
      <el-table-column
        width="200"
        :label="$t('common.operation')">
        <template slot-scope="scope">
          <el-button type="text" size="mini" icon="fa fa-eye" @click="view(scope.row)">查看</el-button>
          <el-button type="text" size="mini" class="btn-text-red" icon="el-icon-delete" @click="deleteProject(scope.row.appId)">{{ $t('common.delete') }}</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      layout="total, prev, pager, next, jumper"
      :total="project.total">
    </el-pagination>
    <el-dialog
      :visible.sync="projectVisible"
      width="35%"
      title="添加项目"
      @close="onCancel"
      center>
      <el-form :model="projectForm" :rules="projectFormRules" ref="projectForm" label-width="120px">
        <el-form-item prop="appId" label="应用ID">
          <el-input v-model.trim="projectForm.appId" maxlength="30"></el-input>
        </el-form-item>
        <el-form-item prop="name" label="应用名称">
          <el-input v-model.trim="projectForm.name" maxlength="30"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="onSubmit">{{ $t('common.ok') }}</el-button>
        <el-button @click="onCancel">{{ $t('common.cancel') }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex'

  export default {
    data() {
      return {
        projectVisible: false,
        projectForm: {},
        currentPage: 1,
        pageSize: 10,
        appId: '',
        projectFormRules: {
          appId: [
            { required: true, message: '请输入应用ID', trigger: 'blur' }
          ],
          name: [
            { required: true, message: '请输入应用名称', trigger: 'blur' }
          ]
        }
      }
    },
    computed: {
      ...mapState({
        project: state => state.config.project
      })
    },
    mounted() {
      const vm = this
      vm.getProjectList(vm)
    },
    methods: {
      ...mapActions('config', ['getProjectList', 'createProject', 'delProject']),
      view(project) {
        const vm = this
        vm.$router.push({
          name: 'configDetail',
          params: {
            id: project.appId
          },
          query: {
            appId: project.appId
          }
        })
      },
      handleCurrentChange(val) {
        const vm = this
        vm.currentPage = val
        vm.getProjectList(vm)
      },
      onCancel() {
        const vm = this
        vm.$refs['projectForm'].resetFields()
        vm.projectVisible = false
      },
      onSubmit() {
        const vm = this
        vm.$refs['projectForm'].validate(async valid => {
          if (valid) {
            const response = await vm.createProject(vm)
            if (response && response.code === 200) {
              vm.$message({
                type: 'success',
                message: '创建成功'
              })
              vm.getProjectList(vm)
              vm.projectVisible = false
            }
          }
        })
      },
      deleteProject(id) {
        const vm = this
        vm.appId = id
        vm.$confirm(`是否确认删除${id}？`, '删除', {
          confirmButtonText: vm.$t('common.ok'),
          cancelButtonText: vm.$t('common.cancel'),
          type: 'warning',
          center: true
        }).then(() => {
          vm.delProject(vm)
        }).catch(() => {})
      }
    }
  }
</script>



